pfe-navigation {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: var(--pfe-theme--color--surface--darkest, #151515) !important;
  padding: 1em;
}

@media screen and (min-width: 960px) {
  pfe-navigation {
    flex-direction: row;
    align-items: center;
  }
  pfe-navigation > pfe-navigation-item {
    margin-left: 1em;
  }
}

pfe-navigation [slot="skip"] {
  display: none;
}

pfe-navigation a {
  color: var(--pfe-theme--color--ui-accent--text, #fff) !important;
  text-decoration: none;
}

pfe-navigation a:visited {
  color: var(--pfe-theme--color--ui-accent--text, #fff) !important;
}

pfe-navigation [slot="mobile-language"],
pfe-navigation [slot="mobile-login"] {
  display: none;
}

pfe-navigation-main {
  display: block;
  flex-grow: 1;
}

pfe-navigation-main ul {
  display: flex;
  flex-direction: column;
  padding-left: 0;
  margin: 0;
}

@media screen and (min-width: 960px) {
  pfe-navigation-main ul {
    justify-content: space-around;
    flex-direction: row;
  }
}

pfe-navigation-main li {
  list-style-type: none;
}

pfe-navigation-item > * {
  font-size: 16px;
  margin-bottom: 0;
  margin-top: 0;
}
